/***** USER *****/
#user-nav {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: end;
  /* box-sizing: border-box; */
}

#user {
  position: relative;
  width: 200px;
  height: 50px;
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: end;
  /* align-items: center; */
}

#user .profile {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

#user .profile h3 {
  text-align: end;
  font-weight: 500;
  line-height: 1em;
  color: #555;
}

#user .profile h3 span{
  font-size: 0.75em;
  color: #777;
}

#user .profile .imgBx {
  position: relative;
  width: 50px;
  height: 50px;
  background: #fff;
  border-radius: 50%;
  border: 0.25px solid;
}

#user .profile .imgBx img {
  /* position: absolute;
  top: 0;
  left: 0; */
  width: 100%;
  height: 100%;
  object-fit: cover;
}
#user .profile .imgBx {
  overflow: hidden;
}

#user .menu {
  position: absolute;
  right: 0;
  top: calc(100% + 1px);
  width: 200px;
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.05);
  padding-left: 0px !important;
}

#user .menu::before {
  content: '';
  position: absolute;
  top: 10px;
  right: 15px;
  width: 20px;
  height: 20px;
  background: #fff;
  transform: rotate(45deg);
}

#user .menu {
  opacity: 0;
  visibility: hidden;
  transition: 0.2s ease;
}

#user .menu.active { 
  opacity: 1;
  visibility: visible;
}

#user .menu ul {
  position: relative;
  display: flex;
  flex-direction: column;
  z-index: 10;
  background: #fff;
  padding-left: 0;
}

#user .menu ul li {
  list-style: none;
  padding: 5px 10px;
  transition: 0.125s;
}

#user .menu ul li:hover {
  background: #f5f5f5;
  transition: 0.125s;
}

#user .menu ul li a{
  text-decoration: none;
  color: #666;
  display: flex;
  align-items: center;
  gap: 10px;
}

#user .menu ul li a ion-icon{
  font-size: 1.25em;
  color: #999;
}

nav li{
  margin-right: 0px;
  margin-left: 15px;
  margin-bottom: 0px;
